.app {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  width: calc(100vw - 15px);
  min-height: 100vh;
}

.app__header,
.app__main,
.app__footer {
  width: 100%;
  max-width: 64rem;
  display: inherit;
}

.app__header,
.app__footer {
  height: 6rem;
  margin: 3rem 6rem;
}

.app__main {
  min-height: calc(100vh - 24rem);
}